@import url(http://fonts.cdnfonts.com/css/diablo);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #161616;
}

#root {
	overflow: hidden;
	height: 100vh;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* width */
::-webkit-scrollbar {
	width: 5px;
	border-radius: 2px;
	height: 3px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #222222;
	border-radius: 2px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #7d0b0d;
	border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #555;
}


.container {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.diablo-text {
  font-family: "Diablo", sans-serif !important;
  font-weight: 900;
  font-style: normal;
}

.caps {
  text-transform: uppercase;
}

.shadow {
  text-shadow: 0px 2px #000000ad;
}

.list-shadow {
  text-shadow: 0px 1px #000000ad;
}

.count-text {
  font-size: 20pt;
}

.unique {
  color: #a59263;
  font-weight: bold;
}

.set {
  color: green;
  font-weight: bold;
}

.rune {
  color: #ffa500;
  font-weight: bold;
}

.crafting {
  color: #ffa500;
  font-weight: bold;
}

.magic {
  color: #4169e1;
}

.totalRunTime {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1101;
}

.listImage-container {
  width: 25px;
  height: 25px;
  overflow: "hidden";
  margin-right: 20px;
}

.lootItem-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-width: 70px;
	white-space: nowrap;
}

.runList-container {
	display: flex;
	max-height: 394px;
	margin-top: 15px;
	position: absolute;
	justify-content: center;
	bottom: 15px;
	overflow: auto;
}

.lootItem-text {
	font-size: 10px;
	margin-top: 5px;
	font-weight: bold;
}

.loot-container {
	display: flex;
	grid-gap: 10px;
	gap: 10px;
	align-items: flex-end;
	flex-flow: wrap;
	width: 100%;
}

.accordation-details-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.one-socket {
  flex: 0 0 20px; /* explanation below */
  margin: 3px;
  display: block;
  min-block-size: 10px;
  max-width: 10px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(84, 84, 84, 0) 0%, rgba(32, 32, 32, 0.3) 60%, rgba(22, 22, 22, 0.4) 100%);
  border: 1px solid rgba(180, 180, 180, 0.377);
}

.socket-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.background-image {
  position: relative;
  height: 60px;
  width: 40px;
  background-image: url(/d2-run-tracker/static/media/no_image.3a4c2d46.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  align-items: center;
  display: flex;
  justify-content: center;
}

.emblem {
  background-image: url(/d2-run-tracker/static/media/emblem.4d9d01bf.png);
  height: 330px;
  background-position-x: center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 821.1px;
  position: relative;

  z-index: -1;
}
.emblem-parent {
  position: absolute;
  height: 200px;
  overflow: hidden;
  margin-top: -180px;
}

.summaryView-container {
	margin-top: 130px;
	width: 100%;
	height: 100%;
	position: relative;
}

.runStats-container {
	display: flex;
	position: relative;
	grid-gap: 30px;
	gap: 30px;
}

.charImg {
	position: relative;
	z-index: 1;
}

.summary-text {
	overflow: hidden;
	white-space: nowrap; /* Don't forget this one */
	text-overflow: ellipsis;
	text-align: center;
	width: 70px;
}

.charShadow {
	border-radius: 50%;
	background: rgb(14, 14, 14);
	-webkit-filter: blur(7px);
	        filter: blur(7px);
	position: absolute;
	opacity: 0.6;
	z-index: 0;
}

.charShadow.amazon {
	bottom: -3px;
	margin-left: 18px;
	width: 60px;
	height: 20px;
}
.charShadow.assassin {
	bottom: -1px;
	margin-left: -3px;
	width: 75px;
	height: 25px;
	opacity: 0.5;
}

.charShadow.barbarian {
	bottom: -3px;
	margin-left: 0px;
	width: 80px;
	height: 20px;
}

.charShadow.druid {
	bottom: -3px;
	margin-left: 0px;
	width: 60px;
	height: 20px;
}

.charShadow.necromancer {
	bottom: -3px;
	margin-left: 10px;
	width: 60px;
	height: 20px;
}

.charShadow.paladin {
	bottom: 0px;
	margin-left: 10px;
	width: 80px;
	height: 35px;
}

.charShadow.sorceress {
	bottom: -4px;
	margin-left: -2px;
	width: 60px;
	height: 20px;
}

.alignData {
	text-align: end;
}

.runStats-data {
	display: flex;
	flex-direction: column;
	align-items: space-between;
}

.dataTable {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: center;
	margin-top: auto;
}

.scrollableItem-container {
	display: flex;
	grid-gap: 15px;
	gap: 15px;
	padding: 15px;
	/* justify-content: space-between; */
	margin-top: 15px;
	/* flex-wrap: wrap; */
	/*overflow-y: auto;*/
}

